import { Component, OnInit } from '@angular/core';
import {CovidDataService} from '../covid-data.service';
import {HostListener} from '@angular/core';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public covidData = {};
  public stats = [];
  public innerWidth:any;
  public statesChart = {}
  public countries = [];
  public country = 'India'
  public history = {};
  public dataSource = {}
  chartTitle = '';
  type = 'PieChart';
  options = {
    is3D: true,
    height : 900
  };
  width = 350;

  constructor(private covidDataService: CovidDataService){
    this.statesChart = {
      'states':[],
      'type' :'BarChart',
      'columnNames' : ['State', 'Positive Cases',{ role: 'style' }, { role: 'annotation' }],
      'chartTitle' : 'States and UTs'
    }
    this.countries = ['India','Usa', 'Italy', 'China', 'Pakistan', 'Canada', 'Germany', 'France', 'Spain', 'Australia'];
    this.dataSource = {
      chart: {
        animation: "0",
        showlabel: "1",
        usehovercolor: "1",
        canvasbordercolor: "FFFFFF",
        bordercolor: "FFFFFF",
        showlegend: "1",
        legendposition: "BOTTOM",
        legendborderalpha: "0",
        legendbordercolor: "ffffff",
        legendallowdrag: "0",
        legendshadow: "0",
        caption: "Map View - States",
        connectorcolor: "000000",
        fillalpha: "80",
        hovercolor: "CDCDCD",
        showborder: 0,
        theme: "fusion"
      },
      colorrange: {
        minvalue: "0",
        startlabel: "Low",
        endlabel: "High",
        code: "e27c25",
        gradient: "1",
        color: [
          { maxvalue: "5", code: "e44a00" },
          { maxvalue: "150", code: "7d0000" },
        ]
      },
      data: [
        { id: "015", value: 2834 },
        { id: "003", value: 3182 },

      ]
    };


  }
  ngOnInit(){
    this.getCovidUpdate(this.country)
    this.getHistory(this.country)
    this.innerWidth = window.innerWidth-50;
    this.getStatesSegrigatedData()
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth-50;
  }
  public getCovidUpdate(country){
    // console.log(country)
    this.covidDataService.getData(country).subscribe(
      (data)=>{this.covidData = data, this.stats = data['latest_stat_by_country'][0]},
      (er)=>console.log(er)
    )
  }
  public getHistory(country){
    this.covidDataService.getHistory(country).subscribe(
      (data)=>{this.history = data},
      (er)=> console.log(er)
    )
  }
  public getStatesSegrigatedData(){
    this.covidDataService.getStatesData().subscribe(
      data=>{this.statesChart['states'] = data['statesArrayData'], this.dataSource['data'] = data['mapdata'], console.log(data)},
      (err)=>console.log(err)
    )
  }


  title = 'covid19 Status ';
}
